/* LotoMania styles.css */


.card {
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  width: 84%;
  justify-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.listContainer {
  overflow-y: auto; /* Adiciona scroll à lista */
  background-color: transparent;
}

.content {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card{
  background-color: #ffffff;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text {
  font-size: 16px;
  color: #585858;
  margin-bottom: 10px;
}

.numberList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.numberCircle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F78100;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 14px;
}

.shareBtn {
  background: none;
  border: none;
  color: #ff1f1e;
  cursor: pointer;
  margin-top: 10px;
}

.share-button .material-icons {
  font-size: 12px;
}

.line {
  border: none;
  height: 1px;
  background-color: #ccc;
  margin: 10px 0;
}



.resultText{
  font-weight: bold;}


